.shootingStar {
   position: absolute;
   left: 0;
   top: 0;
   -webkit-transform: rotateZ(65deg);
           transform: rotateZ(65deg);
   height: 2px;
   -webkit-animation-delay: 2000ms;
           animation-delay: 2000ms;
   background: linear-gradient(-65deg, #7d8592de, rgba(0, 0, 255, 0));
   /* border-radius: 999px; */
   -webkit-filter: drop-shadow(0 0 6px #7d8592);
           filter: drop-shadow(0 0 6px #7d8592);
   -webkit-animation: tail 6000ms ease-in-out infinite,
      shoot 6000ms ease-in-out infinite;
           animation: tail 6000ms ease-in-out infinite,
      shoot 6000ms ease-in-out infinite;
}

.shootingStar::before,
.shootingStar::after {
   content: "";
   position: absolute;
   top: calc(40%-2px);
   right: 2px;
   height: 2px;
   -webkit-animation-delay: 2000ms;
           animation-delay: 2000ms;
   background: linear-gradient(
      -65deg,
      rgba(0, 0, 255, 0),
      #7d8592de,
      rgba(0, 0, 255, 0)
   );
   -webkit-transform: translateX(50%) rotateZ(65deg);
           transform: translateX(50%) rotateZ(65deg);
   border-radius: 100%;
   -webkit-animation: shine 6000ms ease-in-out infinite;
           animation: shine 6000ms ease-in-out infinite;
}

.shootingStar::after {
   -webkit-transform: translateX(50%) rotateZ(-65deg);
           transform: translateX(50%) rotateZ(-65deg);
}
