@-webkit-keyframes tail {
   0% {
      width: 0px;
   }
   30% {
      width: 100px;
   }
   100% {
      width: 0px;
   }
}

@keyframes tail {
   0% {
      width: 0px;
   }
   30% {
      width: 100px;
   }
   100% {
      width: 0px;
   }
}
@-webkit-keyframes shine {
   0% {
      width: 0px;
   }
   60% {
      width: 30px;
   }
   100% {
      width: 0px;
   }
}
@keyframes shine {
   0% {
      width: 0px;
   }
   60% {
      width: 30px;
   }
   100% {
      width: 0px;
   }
}
@-webkit-keyframes shoot {
   0% {
      -webkit-transform: translate(0, 0) rotateZ(65deg);
              transform: translate(0, 0) rotateZ(65deg);
   }
   100% {
      -webkit-transform: translate(40vw, 85vh) rotateZ(65deg);
              transform: translate(40vw, 85vh) rotateZ(65deg);
   }
}
@keyframes shoot {
   0% {
      -webkit-transform: translate(0, 0) rotateZ(65deg);
