header {
   min-height: 100vh;
   width: 100%;
   display: -webkit-flex;
   display: flex;
   -webkit-flex-direction: column;
           flex-direction: column;
   -webkit-justify-content: center;
           justify-content: center;
   -webkit-align-items: center;
           align-items: center;
   font-weight: normal;
   background-image: radial-gradient(ellipse, #242c3a, #0c0f14);
   color: #ebecee;
}

header h1 {
   z-index: 1;
   text-align: center;
   margin: 4rem auto;
   font-family: "EB Garamond", serif;
   font-size: 7.5vw;
   letter-spacing: 0.3rem;
   -webkit-perspective: 10rem;
