   overflow: hidden;
   white-space: nowrap;
   border-right: 0.075em solid #ebecee;
   -webkit-animation: typing 3s steps(18, end), blink-caret 1s step-end infinite;
           animation: typing 3s steps(18, end), blink-caret 1s step-end infinite;
}

