/* The typing effect */
@-webkit-keyframes typing {
   from {
      width: 0;
   }
   to {
      width: 80%;
   }
}
@keyframes typing {
   from {
      width: 0;
   }
   to {
      width: 80%;
   }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
   from,
   to {
      border-color: #ebecee;
   }
   50% {
      border-color: transparent;
   }
}
@keyframes blink-caret {
   from,
   to {
      border-color: #ebecee;
   }
   50% {
      border-color: transparent;
   }
}

/* The fade in effect */
@-webkit-keyframes fade-in {
   from,
   to {
      opacity: 0;
   }
   100% {
      opacity: 1;
   }
}
@keyframes fade-in {
   from,
   to {
      opacity: 0;
   }
   100% {
      opacity: 1;
   }
}

